<template>
	<loading v-model:active="isLoading"
      :is-full-page="true"
      loader= "dots"
      />

    <section id="Total_City" v-if="!is_Single_City">
          <div class="city_container"  v-for="(city, index) in master_citys" :key="index" @click="goToSingleMasterCity(index)">
            <img :src="city.img" :alt="city.name">
            <div class="txt">
              <h2 class="text-lg">{{city.name}}</h2>
            </div>
          </div>

          <div class="city_container" @click="goToSingleOtherCity(0)">
            <img src="@/assets/food_city/Other.jpg" alt="">
            <div class="txt">
              <h2 class="text-lg"></h2>
            </div>
          </div>

    </section>


    <section id="Single_City" class="flex flex-col items-center p-4" v-if="is_Single_City && isLoading === false">
		<h1 class="text-2xl font-bold">{{choose_City}}</h1>
        <button class="px-4 py-2 my-4 text-lg bg-blue-500 hover:bg-blue-700 transition duration-300 text-white rounded" @click="goToTotalCity"></button>
		
		<div v-if="is_Master_City === false" id="other_city" class="flex flex-wrap my-2">
			<span class="cursor-pointer text-white rounded text-base px-2 py-1 m-1" 
				:class="choose_City === city.name ? 'bg-red-500' : 'bg-gray-500'"
				v-for="(city, index) in other_citys" :key="index"
				@click="goToSingleOtherCity(index)">
				{{city.name}}
			</span>
		</div>

		<div v-if="travel_Length() > 0">
			<input type="text" v-model="filter_Input" class="text-lg rounded-md border-2 border-black  px-3 py-1 w-full outline-none" placeholder=" / ">
			<span v-if="filter_Result.length === 0" class="text-base font-bold mb-4">
				
			</span>
			<div v-if="filter_Result.length > 0">
				<div class="my-4 p-4 mx-1 bg-yellow-200 rounded" v-for="(_, index) in pageRow_Route()" :key="index">
					<h1 class="font-bold text-xl">{{filter_Result[currentTotalPage + index].Name}}</h1>
					<div class="my-2">
						<span class="text-base font-light rounded bg-green-600 text-white px-3 py-1 mr-1" v-if="filter_Result[currentTotalPage + index].Class">{{filter_Result[currentTotalPage + index].Class}}</span>
					</div>
					<p class="text-lg"><i class="fas fa-map-marker-alt mr-1 text-red-500"></i>{{filter_Result[currentTotalPage + index].Address}}</p>
					<p class="text-lg"><i class="fas fa-phone-square-alt mr-1 text-green-600"></i> {{ filter_Result[currentTotalPage + index].Phone ? filter_Result[currentTotalPage + index].Phone : "" }}</p>
					<p class="text-lg my-4">
						<span class="rounded-md p-1 font-bold"><i class="far fa-clock mr-1 text-yellow-600"></i></span>
						<br>
						{{filter_Result[currentTotalPage + index].OpenTime ? filter_Result[currentTotalPage + index].OpenTime : '' }}
					</p>
					<button class="bg-red-500 text-base text-white rounded-lg px-3 py-2 hover:bg-red-700 transition duration-300"
						@click="goTo_Position(currentTotalPage + index)">
						<i class="fas fa-location-arrow"></i>
						
					</button>
				</div>

				<Paginator :pageLinkSize="4" :rows="10" :totalRecords="filter_Result.length" :rowsPerPageOptions="[10,20,30]" @page="onPage($event)"
					template="PrevPageLink PageLinks  NextPageLink RowsPerPageDropdown">
					<template #right="slotProps">
						<h1 class="mt-3">
						: {{slotProps.state.rows}}: {{ filter_Result.length }}
						</h1>
					</template>
				</Paginator>
			</div>



		</div>

		
	</section>
  

</template>

<script>
import { reactive, ref } from '@vue/reactivity'
import { get_Restaurant } from "../api/api.js"; 
import {useStore} from 'vuex';
import L from 'leaflet';
import Paginator from 'primevue/paginator';
import Loading from 'vue-loading-overlay';
import { computed } from '@vue/runtime-core';
export default {
    name: 'Food',
	components: {
		Paginator: Paginator,
		Loading: Loading,
	},
    setup(){
		const store = useStore();
		const map = store.state.module_Map.map;
        const master_citys = reactive([
			{name:'', en:'Taipei', img: require('@/assets/food_city/Taipei.jpg')}, 
			{name:'', en:'NewTaipei',img: require('@/assets/food_city/NewTaipei.jpg')}, 
			{name:'', en:'Taoyuan', img: require('@/assets/food_city/Taoyuan.jpg')},
			{name:'', en:'Taichung',img: require('@/assets/food_city/Taichung.jpg')},
			{name:'', en:'Tainan', img: require('@/assets/food_city/Tainan.jpg')}, 
			{name:'', en:'Kaohsiung',img: require('@/assets/food_city/Kaohsiung.jpg')}, 
        ])

		const other_citys = reactive([
			{name:'', en:'Keelung'},{name:'', en:'Hsinchu'},{name:'', en:'HsinchuCounty'},
			{name:'', en:'MiaoliCounty'},{name:'', en:'ChanghuaCounty'},{name:'', en:'NantouCounty'},
			{name:'', en:'YunlinCounty'}, {name:'', en:'ChiayiCounty'},{name:'', en:'Chiayi'}, 
			{name:'', en:'PingtungCounty'}, {name:'', en:'YilanCounty'}, {name:'', en:'HualienCounty'}, 
			{name:'', en:'TaitungCounty'}, {name:'', en:'KinmenCounty'}, {name:'', en:'PenghuCounty'}, 
			{name:'', en:'LienchiangCounty'},
		])

    const PTX_Food_Arr = ref([]);
    const choose_City = ref('');
    const is_Single_City = ref(false);
		const is_Master_City = ref(false);
		const filter_Input = ref('');

		// Loading Config
		const isLoading = ref(false);

		// Page Config
		const currentPage = ref( 0 ); // 
		const pageRow = ref( 10 );		// 
		const currentTotalPage = ref(0);  // 

        const goToSingleMasterCity = (index)=>{
			isLoading.value = true;
			setTimeout(()=>{
				isLoading.value = false;
			}, 1500);
            is_Single_City.value = true;
            is_Master_City.value = true;
			choose_City.value = master_citys[index].name;
            get_Restaurant(master_citys[index].en)
              .then( 
                  (res)=>{
                    PTX_Food_Arr.value = res.data ;
                  }
              )
        }

        const goToSingleOtherCity = (index)=>{
            is_Single_City.value = true;
            is_Master_City.value = false;

			choose_City.value = choose_City.value === "" ? "" : other_citys[index].name;

            get_Restaurant(other_citys[index].en)
              .then( 
                  (res)=>{
                    PTX_Food_Arr.value = res.data ;
                  }
              )
        }

        const goToTotalCity = ()=>{
          is_Single_City.value = false;
          PTX_Food_Arr.value = [];
			choose_City.value = "";
        }

			// The Length of Travel Array
		const travel_Length = ()=> PTX_Food_Arr.value.length;

		// Page Event
        const onPage = (event)=>{
            currentPage.value = event.page;
            pageRow.value = event.rows;
			currentTotalPage.value = currentPage.value * pageRow.value;
        }

		const filter_Result = computed(()=>PTX_Food_Arr.value.filter( (view)=> view.Name.includes(filter_Input.value)));
		const filter_Result_Length = computed(()=> PTX_Food_Arr.value.filter((view)=> view.Name.includes(filter_Input.value)).length );
		// PageRow:   - ( n * m )
		const pageRow_Route = ()=> filter_Result_Length.value - currentTotalPage.value > pageRow.value ? pageRow.value: filter_Result_Length.value - currentTotalPage.value;
		


		let fly_Marker;
		const goTo_Position = (index)=>{
			const lat = filter_Result.value[index].Position.PositionLat;
			const lng = filter_Result.value[index].Position.PositionLon;
			if (fly_Marker) map.removeLayer(fly_Marker);
			let content = `
				<h1 class="text-xl font-bold">${filter_Result.value[index].Name}</h1>
        <p class="text-base">${filter_Result.value[index].Description}</p>
			`;
			fly_Marker = L.marker([lat, lng], {
				icon: store.state.module_Marker.redIcon
			}).bindPopup(content).openPopup().addTo(map);
			map.flyTo([lat, lng], 16, {
				duration: 3 // 
			});
			// 3000(3) popup
			setTimeout(function () {
				L.popup({
					closeButton: false, //  popup X 
					offset: L.point(0, -35) //  popup  icon
				}).setLatLng([lat, lng]) // 
				.setContent(content) // 
				.openOn(map) // 
			}, 3000);
		}

	

        return {master_citys, other_citys, PTX_Food_Arr, choose_City, is_Single_City, is_Master_City, currentPage, pageRow, currentTotalPage, filter_Input, filter_Result_Length, isLoading,
		goToSingleMasterCity, goToSingleOtherCity, goToTotalCity, goTo_Position, onPage, pageRow_Route, travel_Length, filter_Result}
    }

}
</script>

<style>
  
/*  */
/* Travel_City */
div.city_container {
	margin: 10px 0;
	display: flex;
	justify-content: center;
	width: 100%;
	/*  item */
	position: relative;
	cursor: pointer;
}

div.city_container:nth-child(1) {
	margin: 0;
}

div.city_container img {
	width: 100%;
	height: auto;
	cursor: pointer;

	/* div */
	/* vertical-align: middle; */
}


.city_container .txt {
	/*  .item */
	position: absolute;

	/*  */
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;

	padding: 20px;

	/*  */
	background-color: rgba(0, 0, 0, .6);

	/* Flex */
	display: flex;
	justify-content: center;
	flex-direction: column;

	/*  */
	opacity: 0;
	transition: opacity .5s;
}

.city_container:hover .txt {
	opacity: 1;
}


.city_container h2 {
	font-size: 28px;
	color: #ff0;
	font-weight: 500;
}

/*  */
.city_container h2:after {
	content: "";
	display: block;

	/* 0%100% */
	width: 0%;
	/*  */
	height: 2px;
	margin: 10px 0;
	background-color: #ff0;
	transition: width .5s .2s;
}

/*  */
.city_container:hover h2:after {
	width: 100%;
}

</style>